import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/HomeView.vue')
  },
  {
    path: '/about',
    name: 'about',
    component: () => import('../views/AboutView.vue'),
    redirect: '/about/shouye',
    children: [
      {
        path: '/about/shouye',
        name: 'biaoge',
        component: () => import('../views/main/ShouYe.vue'),
        redirect: '/about/shouye/one',
        children: [
          {
            path: '/about/shouye/one',
            name: 'biaoge',
            component: () => import('../views/main/ShouYe/ShouYeo.vue'),
          },
          {
            path: '/about/shouye/tow',
            name: 'biaoge',
            component: () => import('../views/main/ShouYe/ShouYet.vue'),
          },
          {
            path: '/about/shouye/three',
            name: 'biaoge',
            component: () => import('../views/main/ShouYe/ShouYes.vue'),
          }
        ]
      },
      {
        path: '/about/qygl',
        name: 'guanli',
        component: () => import('../views/main/QiyuGuanli.vue'),
        redirect: '/about/qygl/one',
        children: [
          {
            path: '/about/qygl/one',
            name: 'biaoge',
            component: () => import('../views/main/QyGl/CaJi.vue'),
          },
          {
            path: '/about/qygl/tow',
            name: 'biaoge',
            component: () => import('../views/main/QyGl/ZiQi.vue'),
          },
        ]
      },
      {
        path: '/about/qxgl',
        name: 'guanli',
        component: () => import('../views/main/QuanXian.vue'),
      },
      {
        path: '/about/huzhuan',
        name: 'guanli',
        component: () => import('../views/main/HuZhuan.vue'),
      }
    ]
  },
  {
    path: '/zhuc',
    name: 'zhuc',
    component: () => import('../views/ZhuCe.vue'),

  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  // 在这里，你可以执行任何需要在每次路由切换前进行的操作
  localStorage.setItem('id', JSON.stringify(to.query))
  Vue.prototype.$eventBus.$emit('route-change', { to, from })
  next()
})
export default router
